.navbar {
  width: 100%;
  height: 98px;
  background-color: rgb(255, 166, 95);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  z-index: 50;
}
.navbar_logo > a > h2 {
  color: #f4f4f4;
  font-size: 1.4rem;
  cursor: pointer;
}
.navbar_links {
  display: flex;
  list-style: none;
  align-items: center;
  /* justify-content: space-between; */
}
.navbar_links > li {
  padding-left: 1.5rem;
}
.navbar_links > li > a {
  text-decoration: none;
  color: #f4f4f4;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
}

.navbar_links > li > a > span {
  display: flex;
  align-items: center;
  margin-left: 8px;
}
.cart_link {
  background: #333;
  padding: 10px;
  border-radius: 8px;
}
.cart_link:hover {
  background: #dd219e;
  color: #f4f4f4;
}

.cartlogo__badge {
  width: 30px;
  height: 30px;
  background: #f4f4f4;
  border-radius: 50%;
  margin-left: 8px;
  color: #171717;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  font-size: 1rem;
}
.hamburger_menu {
  width: 30px;
  height: 30px;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
.hamburger_menu > div {
  width: 100%;
  height: 3px;
  background: #f4f4f4;
}
.hamburger_menu:hover > div {
  background: #dd219e;
}
@media (max-width: 960px) {
  .navbar_links {
    display: none;
  }
  .hamburger_menu {
    display: flex;
  }
}
@media (max-width: 500px) {
  .navbar {
    height: 60px;
  }
  .navbar_logo > a > h2 {
    font-size: 1rem;
  }
  .hamburger_menu {
    height: 20px;
  }
}
