.cartscreen {
  display: flex;
  max-width: 1300px;
  margin: 2rem auto;
}
.cartscreen h2 {
  margin-bottom: 1rem;
}
.cartscreen_left {
  flex: 0.7;
  margin-right: 1rem;
  padding: 1rem;
}
.cartscreen_right {
  flex: 0.3;
  background: #fff;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
  height: fit-content;
  margin-right: 1rem;
}

.cartscreen_right div {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding: 1rem;
}
.cartscreen_right div:last-child {
  border-bottom: none;
}
.cartscreen_info p {
  padding: 8px;
}
.cartscreen_right > div > button {
  padding: 10px 17px;
  width: 100%;
  background: rgb(255, 166, 95);
  color: #f4f4f4;
  border: 1px solid rgb(255, 166, 95);
  cursor: pointer;
}
.cartscreen_right > div > button:hover {
  opacity: 0.9;
}
@media (max-width: 1320px) {
  .cartscreen {
    max-width: 900px;
  }
}
@media (max-width: 960px) {
  .cartscreen {
    max-width: 800px;
    flex-direction: column;
  }
  .cartscreen_left {
    margin: 0;
  }
  .cartscreen_right {
    margin: 1rem;
  }
}
